body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  /*text-align: center;*/
  color: aliceblue;
  background-image: linear-gradient(to top right, #063968, #063968, #010A27);
  background-attachment: fixed;
  min-height: 100vh;
  height: -webkit-max-content;
  height: max-content;
  /*position: relative;*/
}

/*footer {*/
/*  position: absolute;*/
/*  bottom: 0;*/
/*}*/
.navbarContainer {
    background: transparent;
}

.navbar_brand {
    color: aliceblue;
    font-family: "Bookman Old Style";
}

#navbarSupportedContent > ul > li > a {
    color: aliceblue;
    padding: 1.5rem;
}
section > h1, h2, h6 {
    text-align: center;
}

section {
    padding-top: 3rem;
}
.headerSection {
    height: 77vh;
}

.imageContainer > img {
    width: 80%;
    max-width: 500px;
    height: auto;
    object-fit: cover;
    border: 2px solid #61dafb;
    border-radius: 50%;
    padding: 0.3rem;
}

.introContainer {
    padding: 1rem;
}

.introContainer > h1 {
    font-size: 4rem;
}

.btn {
    margin: 1rem;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: relative;

}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: #0D6EFD;
}
.about__icon {
    color: #61dafb;
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: aliceblue;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: aliceblue;
}

.aboutImageContainer > img {
    object-fit: cover;
    overflow: hidden;
    margin: 20%;
    width: 80%;
    height: auto;
    max-width: 350px;
    border: transparent;
    border-radius: 2rem;
    background: #61dafb;
}
.skillsContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
}

.serviceTitle {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
}
.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    width: -webkit-min-content;
    width: min-content;
}

.contact__option {
    background: royalblue;
    padding: 0.5rem;
    margin: 1rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    /*transition: var(--transition);*/
}

.contact__option:hover {
    background: transparent;
    border-color: royalblue;
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* form */

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid royalblue;
    resize: none;
    color: aliceblue;
}
.blogContainer {
    color: black;
    min-height: 100vh;
}

.articleContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    padding: 1.5rem;
    /*padding: 0 3rem;*/
}

section {
    padding-top: 0;
}


/* ********* media queries (medium devices) ******** */
@media screen and (max-width : 1024px) {
    .articleContainer {
        grid-template-columns: 1fr 1fr;
    }
}

/* ********* media queries (medium devices) ******** */
@media screen and (max-width : 600px) {
    .articleContainer {
        grid-template-columns: 1fr;
    }
}
.article-card {
    overflow: hidden;
    height: 32rem;
    /*width: 24rem;*/
    border: 1px solid lightblue;
    border-radius: 0.7rem;
    position: relative;
}

.article {
    max-height: 14rem;
    overflow: hidden;
    padding-bottom: 3rem;
    text-overflow: ellipsis;
}

.articleText {
    margin: 1rem;
    color: lightgray;
    font-size: 1.2rem;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3; /* start showing ellipsis when 3rd line is reached */
    white-space: pre-wrap; /* let the text wrap preserving spaces */
}

.articleCover {
    height: 15rem;
    width: 100%;
    object-fit: cover;
    overflow: hidden;
}

.articleHeader {
    font-size: 2rem;
    color: aliceblue;
    text-align: center
}

.author {
    position: absolute;
    bottom: 0;
    right: 1rem;
}

/* ********* media queries (medium devices) ******** */
@media screen and (max-width : 600px) {
    .article-card {
        height: -webkit-min-content;
        height: min-content;
    }
}
.authContainer {
    min-height: 50vh;
}

